@import url('https://fonts.googleapis.com/css2?family=Anton&family=Montserrat:wght@400;600&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    padding-top:35px;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    background-color: #000;
    color: #fff;
}

h1 {
    font-weight: 400;
    font-size: 190px;
    font-family: 'Anton';
    text-transform: uppercase;
}

h2 {
    font-size: 170px;
    font-family: 'Anton';
    text-transform: uppercase;

    .title-bottom {
        font-size: 188px
    }
}

h3 {
    font-size: 20px;
    font-weight: 600px;
    text-transform: uppercase;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
    color: #fff;
}

p {
    color: rgba(255, 255, 255, 0.5);
}

.container {
    max-width: 1400px;
    margin: auto;
    padding: 0 35px;
}

header {
    display: flex;
    justify-content: space-between;
}

.header__navigation {
    display: none;
}

.agency {
    position: relative;
    display: flex;
    justify-content: flex-end;
    margin-top: 150px;
    margin-bottom: 200px;
}

.agency__title-link {
    position: absolute;
    width:60%;
    top: -30px;
    left: 0;
}

.agency__title {
    display: flex;
    flex-direction: column;
    margin: 0;
}

.title-top {
    z-index: -1;
    align-self: flex-end;
}

.agency__title-top {
    color: #000;
    text-shadow: 0px 0px 4px #fff;
}

.agency__title-bottom {
    font-size: 230px;
    margin-top: -50px;
}

.link {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width:205px;
    height:65px;
    margin-top:50px;
    padding:0px 30px;
    background-color: #fff;
    color: #000;  
    
    &::after{
        content: '';
        display: inline-block;
        background-color: white;
        border-right: 1px solid black;
        border-bottom: 1px solid black;
        padding: 5px;
        transform: rotate(-45deg);
    }
}


.agency__text-video {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 70%;

    .agency__text {
        width: 325px;
        margin-bottom: 25px;
        text-align: left;
        line-height: 23px;
    }

}

video {
    width:100%;
    object-fit: cover;
}
